<template>
  <div id="trash" class="detail">
    <div class="note-sidebar">
      <h3 class="notebook-title"></h3>
      <div class="menu">
        <div></div>
        <div></div>
      </div>
      <ul class="notes">
        <li v-for="note in trashNotes" :key="note.id">
          <router-link :to="`/trash?noteId=${note.id}`">
            <span class="date">{{ note.updatedAtFriendly }}</span>
            <span class="title">{{ note.title }}</span>
          </router-link>
        </li>
      </ul>
    </div>
    <div class="note-detail">
      <div class="note-bar" v-if="true">
        <span>{{ curTrashNote.createdAtFriendly }}</span>
        <span>|</span>
        <span>{{ curTrashNote.updatedAtFriendly }}</span>
        <span>|</span>
        <span>{{ belongTo }}</span>

        <a class="btn action" @click="onRevert"></a>
        <a class="btn action" @click="onDelete"></a>
      </div>
      <div class="note-title">
        <span>{{ curTrashNote.title }}</span>
      </div>
      <div class="editor">
        <div class="preview markdown-body" v-html="compiledMarkdown"></div>
      </div>
    </div>
  </div>
</template>

<script>
// import Auth from "@/apis/auth";
import MarkdownIt from "markdown-it";
import { mapGetters, mapActions, mapMutations } from "vuex";
// import Trash from "@/apis/trash";

let md = new MarkdownIt();

export default {
  data() {
    return {};
  },

  created() {
    // Auth.getInfo().then(res => {
    //   if (!res.isLogin) {
    //     this.$router.push({ path: "/login" });
    //   }
    // });
    this.checkLogin({ path: "/login" });
    this.getNotebooks()
    this.getTrashNotes().then(() => {
      this.setCurTrashNote({ curTrashNoteId: this.$route.query.noteId });
      this.$router.replace({
        path:'/trash',
        query:{noteId:this.curTrashNote.id}
      })
    });
  },

  computed: {
    ...mapGetters(["trashNotes", "curTrashNote",'belongTo']),

    compiledMarkdown() {
      return md.render(this.curTrashNote.content || "");
    }
  },

  methods: {
    ...mapMutations(["setCurTrashNote"]),

    ...mapActions([
      "checkLogin",
      "deleteTrashNote",
      "revertTrashNote",
      "getTrashNotes",
      'getNotebooks'
    ]),

    onDelete() {
      this.$confirm('','',{
        confirmButtonText:'',
        cancelButtonText:'',
        type:'warning'
      }).then(() => {
        return this.deleteTrashNote({ noteId: this.curTrashNote.id })
      }).then(() => {
        this.setCurTrashNote({});
        this.$router.replace({
          path:'/trash',
          query:{noteId:this.curTrashNote.id}
        })
      })
    },

    onRevert() {
      this.revertTrashNote({ noteId: this.curTrashNote.id }).then(() => {
        this.setCurTrashNote();
        this.$router.replace({
          path:'/trash',
          query:{noteId:this.curTrashNote.id}
        })
      })
    }
  },

  beforeRouteUpdate(to, from, next) {
    this.setCurTrashNote({ curTrashNoteId: to.query.noteId });
    next();
  }
};
</script>

<style lang="less">
@import url(../assets/css/note-sidebar.less);
@import url(../assets/css/note-detail.less);

#trash {
  display: flex;
  align-items: stretch;
  background-color: #fff;
  flex: 1;

  .note-bar {
    .action {
      float: right;
      margin-left: 10px;
      padding: 2px 4px;
      font-size: 12px;
    }
  }
}
</style>



